 padding: 8px;\r\n  text-decoration: none;\r\n  font-size: 36px;\r\n  color: #818181;\r\n  display: block;\r\n  /* Display block instead of inline */\r\n  transition: 0.3s;\r\n  /* Transition effects on hover (color) */\r\n}\r\n/* When you mouse over the navigation links, change their color */\r\n.overlay a:hover, .overlay a:focus {\r\n  color: #f1f1f1;\r\n}\r\n/* Position the close button (top right corner) */\r\n.overlay .closebtn {\r\n  position: absolute;\r\n  top: 20px;\r\n  right: 45px;\r\n  font-size: 60px;\r\n}\r\n/* When the height of the screen is less than 450 pixels, change the font-size of the links and position the close button again, so they don't overlap */\r\n@media screen and (max-height: 450px) {\r\n  .overlay a {\r\n    font-size: 20px\r\n  }\r\n\r\n  .overlay .closebtn {\r\n    font-size: 40px;\r\n    top: 15px;\r\n    right: 35px;\r\n  }\r\n}\r\n/* Navigation bar (Need help?/Menu) END */\r\n/*input active border\r\ninput[type=text], [type=password], input[type=number], [type=email], [type=url], textarea {\r\n  -webkit-transition: all 0.10s ease-in-out;\r\n  -moz-transition: all 0.10s ease-in-out;\r\n  -ms-transition: all 0.10s ease-in-out;\r\n  -o-transition: all 0.10s ease-in-out;\r\n  outline: none;\r\n  padding: 3px 0px 3px 3px;\r\n  margin: 5px 1px 3px 0px;\r\n  border: 1px solid #FFFFFF;\r\n}\r\n\r\ninput[type=text]:focus, [type=password]:focus, input[type=number]:focus, [type=email]:focus, [type=url]:focus, textarea:focus {\r\n  font-family: 'Quicksand', sans-serif;\r\n  border: 2px solid #ff6700;\r\n  padding: 3px 0px 3px 3px;\r\n  margin: 5px 1px 3px 0px;\r\n  background: #FFFFFF;\r\n  color: #146eff;\r\n}*/\r\n@media all and (min-width: 600px) {}\r\n@media all and (min-width: 800px) {\r\n  .logo {\r\n    order: 0;\r\n  }\r\n\r\n  .main {\r\n    order: 2;\r\n  }\r\n\r\n  .progress {\r\n    order: 3;\r\n  }\r\n\r\n  .footer {\r\n    order: 4;\r\n  }\r\n}\r\n.col-3{ margin: 40px 3%; position: relative;z-index:5;}\r\n/* necessary to give p